section.sidebar {
  top: 0;
  left: 0;
  width: 400px;
  height: 100vh;
  z-index: 500;
  padding: 1rem;
  position: fixed;
  background: rgba(52, 53, 56, 0.3647058824);
  overflow-y: auto;
  scrollbar-color: rgba(255, 255, 255, 0.1568627451) #19191b;
  scrollbar-width: thin;
  border-right: 1px solid rgba(255, 255, 255, 0.1568627451);
  backdrop-filter: blur(5px);
}
section.sidebar h2 {
  margin-top: 0;
  font-size: 1.5rem;
  font-weight: 500;
}